<template>
<page-card
  :title="title"
  icon="none"
>
  <template v-slot:actions>
    <v-btn icon="mdi-content-save" to="/returns"></v-btn>
    <v-btn icon="mdi-arrow-left" @click="this.$router.back"></v-btn>
  </template>
  <v-tabs v-model="tab" v-if="edit===true" >
    <v-tab v-for="item in tabsEdit" :key="item" >{{item}}</v-tab>
  </v-tabs>
  <v-tabs v-model="tab" v-else>
    <v-tab v-for="item in tabsAdd" :key="item" >{{item}}</v-tab>
  </v-tabs>
  <v-card v-if="tab===0">
    <returns-form></returns-form>
  </v-card>
  <v-card v-else>
    <return-history-form/>
  </v-card>
</page-card>
</template>

<script>
import PageCard from '../../../components/PageCard'
import ReturnsForm from './forms/ReturnForm'
import ReturnHistoryForm from './forms/ReturnHistoryForm'
export default {
  name: 'ReturnsEdit',
  components: { ReturnHistoryForm, ReturnsForm, PageCard },
  data () {
    return {
      title: '',
      tabsEdit: ['Основное', 'История'],
      tabsAdd: ['Основное'],
      tab: 0
    }
  },
  props: ['edit'],
  mounted () {
    if (this.edit === true) {
      this.title = 'Редактирование'
    } else {
      this.title = 'Создание'
    }
  }
}
</script>

