<template>
  <v-card-title>{{$t('return.titles.history')}}</v-card-title>
  <v-data-table
    :headers="headersHistory"
    :items="itemsHistory"
  >
    <template #item-action>
      <v-btn
        icon="mdi-pencil-outline"
        variant="text"
        size="small"
        color="blue"
      />
    </template>
  </v-data-table>
  <v-card-title>{{$t('return.titles.addReturn')}}</v-card-title>
  <v-select
    :lable="$t('return.fields.action')"
    :items="returnDetail"
    v-model="newHistory.status"
  />
  <v-checkbox
    :label="$t('return.fields.notify')"
    v-model="newHistory.notify"
  />
  <v-textarea
    :label="$t('return.fields.comment')"
    v-model="newHistory.description"
  />
  <v-btn @click="addHistory">{{$t('return.buttons.add')}}</v-btn>
</template>

<script>
export default {
  name: 'ReturnHistoryForm',
  data () {
    return {
      headersHistory: [
        { text: this.$t('return.tables.createdAt'), value: 'createdAt', sortable: true },
        { text: this.$t('return.tables.comment'), value: 'description', sortable: false },
        { text: this.$t('return.tables.status'), value: 'status', sortable: false },
        { text: this.$t('return.tables.notify'), value: 'notify', sortable: false }
      ],
      returnDetail: ['Awaiting products', 'Complete', 'Pending'],
      itemsHistory: [],
      newHistory: {
        status: '',
        notify: false,
        description: ''
      }
    }
  },
  methods: {
    addHistory () {
      const payload = {
        createdAt: Date.now(),
        status: this.newHistory.status,
        notify: this.newHistory.notify,
        description: this.newHistory.description
      }
      this.itemsHistory.push(payload)
      this.newHistory = {
        status: '',
        notify: false,
        description: ''
      }
    }
  }
}
</script>

